@import url('https://fonts.googleapis.com/css2?family=Yusei+Magic&display=swap');



*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  outline: none;
}
body{
  width: 100%;
  height: 100%;
  background-color: rgb(255, 255, 255);
  font-family: 'Yusei Magic', sans-serif;
}
a{
  text-decoration: none;
  font-family: 'Yusei Magic', sans-serif;
  color: #fd7094;
}
a:hover {
  color: hotpink;
text-decoration: none;
background-size: auto 6px;
background-repeat: repeat-x;
background-position: 0 100%;
background-image: url("data:image/svg+xml;charset=utf8,%3Csvg id='squiggle-link' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' xmlns:ev='http://www.w3.org/2001/xml-events' viewBox='0 0 20 4'%3E%3Cstyle type='text/css'%3E.squiggle{animation:shift .3s linear infinite;}@keyframes shift {from {transform:translateX(0);}to {transform:translateX(-20px);}}%3C/style%3E%3Cpath fill='pink' stroke='pink' stroke-width='2' class='squiggle' d='M0,3.5 c 5,0,5,-3,10,-3 s 5,3,10,3 c 5,0,5,-3,10,-3 s 5,3,10,3'/%3E%3C/svg%3E");
}

a:focus {
color: hotpink;
}
h1.page-header{
  margin-bottom: 10px;
  font-weight: 700;
  text-align: start;
 font-size: 60px;
  background: linear-gradient(to right, #7bdff2, #ff70a6);
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  top: 0;
  background-color: #83dff2;
  color: #fff;
 }
 .nav-links a {
  color: #fff;
 }

 .logo {
  font-size: 28px;
 }
 
 .menu {
  display: flex;
  gap: 1em;
  font-size: 18px;
 }
 .menu li:hover {
  background-color: #04cdf5;
  border-radius: 12px;
  transition: 0.3s ease;
 }
 .menu li {
  padding: 5px 14px;
 }

 .services {
  position: relative; 
 }
 .dropdown {
  background-color:#83dff2;
  padding: 1em 0;
  position: absolute; 
  display: none;
  border-radius: 8px;
  top: 35px;
 }
 .dropdown li + li {
  margin-top: 10px;
 }
 .dropdown li {
  padding: 0.5em 1em;
  width: 8em;
  text-align: center;
 }
 .dropdown li:hover {
  background-color: #83dff2;
 }
 .services:hover .dropdown {
  display: block;
 }

 input[type=checkbox]{
  display: none;
 } 
 
 .hamburger {
  display: none;
  font-size: 24px;
  user-select: none;
 }

 @media (max-width: 768px) {
 .menu { 
  display:none;
  position: absolute;
  background-color:#83dff2;
  right: 0;
  left: 0;
  text-align: center;
  padding: 16px 0;
 }
 .menu li:hover {
  display: inline-block;
  background-color:#04cdf5;
  transition: 0.3s ease;
 }
 .menu li + li {
  margin-top: 12px;
 }
 input[type=checkbox]:checked ~ .menu{
  display: block;
 }
 .hamburger {
  display: block;
 }
 .dropdown {
  left: 50%;
  top: 30px;
  transform: translateX(35%);
 }
 .dropdown li:hover {
  background-color: #04cdf5;
 }
 }
h1,h2,h3,h4,h5,h6,span{
  font-size: 18px;
  color:#fd7094;  font-family: 'Yusei Magic', sans-serif;
}
li{
  overflow: hidden;

}
span{
  display: block;
}
p{
  font-size: 15px;
  font-family: 'Yusei Magic', sans-serif;
}

.display-flex{
  display: flex;
}
.align-center{
  align-items: center;
}
.justify-center{
  justify-content: center;
}
.justify-space-between{
  justify-content: space-between;
}
.margin-15{
  margin: 0px 15px;
}
.paddingx-30{
  padding: 0px 30px;
}
.paddingxy-30{
  padding: 30px;
}
.container{
  width: 92%;
  margin: 0px auto;
}
.paddingz-10{
  padding: 10px 0px;
}
.sweet{
  color: #ffa9c4;
}
.row{
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  overflow: hidden;
  align-items: flex-start;
  justify-content: center;
}
.col-2{
  max-width: 50%;
  display: flex;
  padding: 20px;
  justify-content: center;
  flex-direction: column;
}
.col-3{
  max-width: 33.333333%;
  display: flex;
  padding: 20px;
  justify-content: center;
  flex-direction: column;
  margin-top: 20px;
}
.comp-space{
  padding: 100px 0px;
}
.pointer{
  cursor: pointer;
}
svg {
  stroke-width: 1!important;
}
.relative{
  position: relative;
}
.absolute{
  position: absolute;
}
strong{
  color: #ff7ca5;
  font-weight: 600;
}
input{
  color: rgba(198,201,216,.75);
}
button.btn {
  font-size: 18px;
  font-weight: 500;
  font-family: 'Yusei Magic', sans-serif;
  border-radius: 5px;
  border: 2px solid #83dff2;
  background-color: #83dff2;
  color: #fff!important;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: .4s all ease;
  height: 50px;
  width: 180px;
  margin: 0px 10px;
  
}
.p__color {
  color: rgb(0, 0, 0);
  font-size: 18px;
  line-height: 30px;
  font-family: 'Yusei Magic', sans-serif;
  margin-bottom: 15px;
}
.white{
  color: #ffffff;
}
.black{
  color: #000000;
}

@media only screen and (max-width: 768px){
.col-2 {
    max-width: 100%;
}
}
@media only screen and (max-width: 768px){
.col-3 {
    max-width: 50%;
}
}
@media only screen and (max-width: 425px){
.col-3 {
    max-width: 100%;
}
}
@media only screen and (max-width: 425px){
span.copyright {
    font-size: 13px;
}
.footer.d__flex.align__items__center.justify__content__space__between.pz-10 {
  flex-wrap: wrap;
}
}
