 }

 @media (max-width: 768px) {
 .menu { 
  display:none;
  position: absolute;
  background-color:#83dff2;
  right: 0;
  left: 0;
  text-align: center;
  padding: 16px 0;
 }
 .menu li:hover {
  display: inline-block;
  background-color:#04cdf5;
  transition: 0.3s ease;
 }
 .menu li + li {
  margin-top: 12px;
 }
